import {
  beforeEach,
  beforeEachProviders,
  describe,
  expect,
  it,
  inject,
} from '@angular/core/testing';
import { ComponentFixture, TestComponentBuilder } from '@angular/compiler/testing';
import { Component } from '@angular/core';
import { By } from '@angular/platform-browser';
import { ActivitiesTaskComponent } from './activities-task.component';

describe('Component: ActivitiesTask', () => {
  let builder: TestComponentBuilder;

  beforeEachProviders(() => [ActivitiesTaskComponent]);
  beforeEach(inject([TestComponentBuilder], function (tcb: TestComponentBuilder) {
    builder = tcb;
  }));

  it('should inject the component', inject([ActivitiesTaskComponent],
      (component: ActivitiesTaskComponent) => {
    expect(component).toBeTruthy();
  }));

  it('should create the component', inject([], () => {
    return builder.createAsync(ActivitiesTaskComponentTestController)
      .then((fixture: ComponentFixture<any>) => {
        let query = fixture.debugElement.query(By.directive(ActivitiesTaskComponent));
        expect(query).toBeTruthy();
        expect(query.componentInstance).toBeTruthy();
      });
  }));
});

@Component({
  selector: 'test',
  template: `
    <sa-activities-task></sa-activities-task>
  `,
  directives: [ActivitiesTaskComponent]
})
class ActivitiesTaskComponentTestController {
}

